import io from 'socket.io-client';
import {baseSocketURL} from "../api/main";
import {CONNECT_SOCKET, GET_SOCKET} from "../mutation-types";


const state = {
    socket: {},
    authenticated: false
}

const getters = {
    [GET_SOCKET]: (state) => {
        return state.socket;
    }
}

const mutations = {
    [CONNECT_SOCKET]: (state) => {
        state.authenticated = true;
    }
}

const actions = {
    [CONNECT_SOCKET]: async ({state, commit}, payload) => {
        if (state.socket.connected) return;
        state.socket = io(baseSocketURL)
        state.socket.on('connect', () => {
            console.log('connected')
            state.socket.emit('authentication', payload)
            state.socket.on('authenticated', () => {
                commit(CONNECT_SOCKET)
            })
        });
        state.socket.on('disconnect', () => {
            state.socket.connect()
        });
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}