<template>
    <div class="user-actions">
        <el-button-group class="w-100" size="small">
            <el-tooltip class="item" effect="dark" content="Контакты" placement="left">
                <el-button class="w-50" icon="far fa-address-card" size="mini" @click="dialogVisible = true"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="Написать" placement="right">
                <el-button type="warning" class="w-50" icon="el-icon-message" size="mini" @click="chatVisible = true"></el-button>
            </el-tooltip>
        </el-button-group>
        <el-dialog
                title="Контакты"
                :visible.sync="dialogVisible"
                width="30%">
            <template v-if="person.credentials.phone || person.credentials.email">
                <p v-if="person.credentials.phone">Телефон: <a target="_blank" :href="'tel:'  +  person.credentials.phone">{{person.credentials.phone}}</a></p>
                <p v-if="person.credentials.email">Почта: <a target="_blank" :href="'mailto:'  +  person.credentials.email">{{person.credentials.email}}</a></p>
            </template>
            <template v-else>
                <h4>Не имеет контактов</h4>
            </template>
        </el-dialog>
        <el-dialog
                :title="'Отправить сообщение ' + (person.firstName ? person.firstName : person.credentials.email)"
                :visible.sync="chatVisible"
                width="30%">
            <el-input type="textarea"
                      placeholder="Введите сообщение"
                      v-model="messageText"
                      @keyup.enter.native="sendMessage()"
                      :autosize="{ minRows: 3}"></el-input>
            <div class="text-right">
                <div class="mt-4">
                    <el-button type="" @click="chatVisible = false" size="small"><span>Отмена</span></el-button>
                    <el-button type="primary" @click="sendMessage" size="small">Отправить <i class="ml-3 fas fa-check"></i></el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {
        ADD_TO_CHAT,
        ADD_TO_CONVERSATION,
        CHAT,
        CONVERSATIONS,
        GET_SOCKET,
        MESSAGES
    } from "../../store/mutation-types";
    import {COMPANY_PROFILE, GET_PROFILE} from "../../store/types/companyProfile";
    export default {
        name: 'user-actions',
        data() {
            return {
                dialogVisible: false,
                chatVisible: false,
                messageText: ''
            }
        },
        props: {
            person: {
                type: Object,
                required: true
            }
        },
        computed: {
            socket() {
                return this.$store.getters[MESSAGES + GET_SOCKET]
            },
            profileId() {
                return this.$store.getters[COMPANY_PROFILE + GET_PROFILE].id
            }
        },
        methods: {
            sendMessage() {
                this.socket.emit('chat_message', {
                    receiverId: this.person.id,
                    messageType: 'text',
                    text: this.messageText,
                    timeSent: new Date(),
                }, data => {
                    this.$router.push('/profile/chat/' + data.conversationId)
                    const message = {
                        timeSent: new Date(),
                        authorId: this.profileId,
                        messageType: 'text',
                        text: this.messageText
                    }
                    this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {message: message.text, chatId: data.conversationId})
                    this.$store.commit(CHAT + ADD_TO_CHAT, {messages: [message].slice(), chatId: data.conversationId})
                })
            }
        }
    }
</script>