<template>
    <div class="user-docs">
        <h1>Ваши документы</h1>
            <el-upload
                    class="file-upload w-100 text-center my-3"
                    drag
                    ref="upload"
                    :action="baseURL + '/student/private/document'"
                    name="file"
                    :accept="'image/png, image/jpg, application/pdf, image/jpeg, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/msword'"
                    :headers="{'Authorization': token}"
                    :on-error="handleError"
                    :before-upload="beforeUpload"
                    :on-success="handleSuccess"
                    :on-change="handleChange"
                    :on-exceed="handleExceed"
                    :limit="3"
                    :file-list="fileList"
                    list-type="picture"
            >
                <div class="d-flex align-items-center justify-content-center p-2">
                    <i class="el-icon-upload"></i>
                    <div class="">
                        <div class="el-upload__text">Перетащите файлы сюда <em>или кликните для выбора файла</em></div>
                        <div class="el-upload__tip" slot="tip">Разрешены только jpg/png/pdf/doc/docx расширения</div>
                    </div>
                </div>
            </el-upload>
            <div class="row">
                <div class="col-md-6 mb-3" v-for="(document, i) in documents" :key="i">
                    <document-item :document="document"></document-item>
                </div>
            </div>
    </div>
</template>
<script>
    import {baseURL} from "../../../store/api/main";
    import {AUTH, GET_TOKEN} from "../../../store/types/auth";
    import {ADD_DOCUMENTS, DOCUMENTS, GET_DOCUMENTS} from "../../../store/mutation-types";
    import DocumentItem from "./DocumentItem";
    export default {
        components: {DocumentItem},
        name: 'user-docs',
        data() {
            return {
                baseURL,
                dialogVisible: false,
                fileList: [],
            }
        },
        computed: {
            token() {
                return this.$store.getters[AUTH + GET_TOKEN]
            },
            documents() {
                return this.$store.getters[DOCUMENTS + GET_DOCUMENTS]
            }
        },
        methods: {
            validateFile(file) {
                if (file.size / 1024 /1024 > 12) {
                    this.$notify.error({
                        title: 'Ошибка',
                        message: 'Размер файла не должен превашать 12MB'
                    })
                    return false
                }
                return true
            },
            handleError(err, file, fileList){
                console.log({err, file, fileList})
            },
            handleChange(file, fileList) {
                if (!this.validateFile(file)) {
                    console.log(fileList, fileList.indexOf(file))
                    fileList.splice(fileList.indexOf(file), 1)
                }
            },
            beforeUpload(file) {
                console.log(file);
                return this.validateFile(file);
            },
            handleSuccess(response, file, fileList) {
                const index = fileList.findIndex(f => f === file)
                if (index!==-1) fileList.splice(index, 1)
                this.$store.commit(DOCUMENTS + ADD_DOCUMENTS, [response].slice())
            },
            handleExceed() {
                this.$message({
                    message: 'Пожайлуйста, подождите пока загрузятся предыдущие файлы',
                    type: 'warning'
                });
            }
        },
        mounted() {
            this.$store.dispatch(DOCUMENTS + GET_DOCUMENTS, 'student')
        }
    }
</script>
<style scoped lang="scss">
    .file-upload /deep/ .el-upload {
        width: 100%;
    }
    .file-upload /deep/ .el-upload-dragger {
        width: 100%;
        height: auto;
    }
    .file-upload /deep/ .el-upload-dragger .el-icon-upload {
        margin: 0;
        font-size: 60px;
    }
</style>