<template>
    <div class="task py-3">
        <template v-if="task">
            <div class="task-item">
                <div class="task-process mb-4">
                    <h2 class="mb-5">Контроль задачи «{{task.vacancyName}}»</h2>
                    <el-progress type="circle" :strokeWidth="20" :percentage="percentage" :width="160"></el-progress>
                </div>
                <div class="task-info" style="max-width: 500px">
                    <div class="row">
                        <div class="col-md-6 pr-0">
                            <p class="task-header">Заказчик</p>
                            <div class="d-flex align-items-center px-3">
                                <div class="round-image mr-2" :style="{backgroundImage: `url(${task.companyImage})`}"></div>
                                <span>{{task.companyName}}</span>
                            </div>
                        </div>
                        <div class="col-md-6 pl-0">
                            <p class="task-header">Исполнитель</p>
                            <div class="d-flex align-items-center px-3">
                                <div class="round-image mr-2" :style="{backgroundImage: `url(${task.studentImage})`}"></div>
                                <span>{{task.freelancerId}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
    import {
        GET_SELECTED_TASK,
        GET_STUDENT_ONGOING_TASKS,
        SET_SELECTED_TASK, TASKS
    } from "../../store/mutation-types";
    export default {
        name: 'user-task',
        computed: {
            task() {
                return this.$store.getters[TASKS + GET_SELECTED_TASK]
            },
            percentage() {
                const today = (new Date()).getTime()
                const endDate = this.task.deadline.getTime()
                const startDate = this.task.startDate.getTime()
                return parseInt((today - startDate) / (endDate - startDate) * 100)
            }
        },
        methods: {
            selectTask(id) {
                this.$store.commit(TASKS + SET_SELECTED_TASK, id)
                if (!this.task) {
                    this.$store.dispatch(TASKS + GET_STUDENT_ONGOING_TASKS)
                }
            }
        },
        watch: {
            $route(to, from) {
                if (to.params.id !== from.params.id) {
                    this.selectTask(to.params.id)

                }
            }
        },
        mounted() {
            this.selectTask(this.$route.params.id)
        }
    }
</script>

<style>
    main.el-main {
        overflow: visible;
    }
</style>