<template>
    <div class="user-actions">
        <el-button-group class="w-100" size="small" v-if="userRole === USER">
            <el-tooltip class="item" effect="dark" content="Подробнее" placement="left">
                <el-button class="w-50" icon="far fa-file-alt" size="mini" @click="dialogVisible = true"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="Написать" placement="right">
                <el-button class="w-50" icon="far fa-envelope" size="mini" @click="chatVisible = true" plain></el-button>
            </el-tooltip>
        </el-button-group>
        <template v-if="userRole === COMPANY">
            <el-button class="ml-auto d-block" @click="dialogVisible = true" round icon="far fa-file-alt"><span class="ml-3">Подробнее</span></el-button>
        </template>
        <el-dialog :title="vacancy.vacancyName"
                :visible.sync="dialogVisible"
                width="50%"
        >
            <p class="mb-4">Компания: {{vacancy.companyName}}</p>
            <p v-if="vacancy.description" v-html="vacancy.description"></p>
            <template v-if="vacancy.demands && vacancy.demands.length > 0">
                <div>
                    <p class="d-inline-block">Требования:</p>
                    <ul class="mb-3 tags-list">
                        <li v-for="(requirement, index) in vacancy.demands" :key="index">
                            {{requirement}}
                        </li>
                    </ul>
                </div>
            </template>
            <div style="opacity: .5">
                <p class="mb-0" v-if="vacancy.cost">Заработная плата: {{vacancy.cost}}</p>
                <p class="mb-0" v-if="vacancy.deadline">Срок сдачи: {{vacancy.deadline | moment('Do MMMM, YYYY')}}</p>
            </div>
        </el-dialog>
        <el-dialog :title="'Отправить сообщение ' + vacancy.companyName"
                :visible.sync="chatVisible"
                width="30%">
            <el-input type="textarea"
                      placeholder="Введите сообщение"
                      v-model="messageText"
                      @keyup.enter.native="sendMessage()"
                      :autosize="{ minRows: 3}"></el-input>
            <div class="text-right">
                <div class="mt-4">
                    <el-button type="" @click="chatVisible = false" size="small"><span>Отмена</span></el-button>
                    <el-button type="primary" @click="sendMessage" size="small">Отправить <i class="ml-3 fas fa-check"></i></el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {
        ADD_TO_CHAT,
        ADD_TO_CONVERSATION, CHAT,
        COMPANY,
        CONVERSATIONS,
        GET_SOCKET,
        MESSAGES,
        USER
    } from "../../store/mutation-types";
    import {AUTH, IS_LOGGED} from "../../store/types/auth";
    import {GET_PROFILE, USER_PROFILE} from "../../store/types/userProfile";
    import {COMPANY_PROFILE} from "../../store/types/companyProfile";
    export default {
        name: 'company-actions',
        data() {
            return {
                dialogVisible: false,
                chatVisible: false,
                messageText: '',
                USER,
                COMPANY,
            }
        },
        props: {
            vacancy: {
                type: Object,
                required: true
            }
        },
        computed: {
            userRole() {
                return this.$store.getters[AUTH + IS_LOGGED]
            },
            profileStore() {
                return this.userRole === USER ? USER_PROFILE : COMPANY_PROFILE
            },
            profileId() {
                return this.$store.getters[this.profileStore + GET_PROFILE].id
            },
            socket() {
                return this.$store.getters[MESSAGES + GET_SOCKET]
            },
        },
        methods: {
            sendMessage() {
                this.socket.emit('chat_message', {
                    receiverId: this.vacancy.companyId,
                    messageType: 'text',
                    text: this.messageText,
                    timeSent: new Date(),
                }, data => {
                    this.$router.push('/profile/chat/' + data.conversationId)
                    const message = {
                        timeSent: new Date(),
                        authorId: this.profileId,
                        messageType: 'text',
                        text: this.messageText
                    }
                    this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {message: message.text, chatId: data.conversationId})
                    this.$store.commit(CHAT + ADD_TO_CHAT, {messages: [message].slice(), chatId: data.conversationId})
                })
            }
        },
    }
</script>