<template>
    <div class="ongoing-tasks-container pr-4 pr-md-0 py-5">
        <h2 class="mb-5">Задачи</h2>
        <ul class="ongoing-tasks row">
            <li class="col-md-6 col-lg-4 mb-5" v-for="(task, i) in tasks" :key="i">
                <div class="ongoing-task">
                    <div class="d-flex task-header">
                        <div class="text-center" style="min-width: 80px">
                            <div class="round-image" :style="{backgroundImage: `url(${task.studentImage})`}"></div>
                        </div>
                        <div class="task-title mt-2 py-1 ">
                            <h4 class="mb-0">{{task.vacancyName}}</h4>
                            <p style="opacity: 0.6">{{task.studentEmail}}</p>
                        </div>
                    </div>
                    <div class="task-content px-4 py-3">
                        <div class="d-flex align-items-center">
                            <div class="task-progress">
                                <el-progress type="circle" :percentage="getPercentage(task.startDate, task.deadline)"></el-progress>
                            </div>
                            <div class="task-info pl-3">
                                <p class="text-right">
                                    <span style="font-size: .7em; opacity: .7">Дата начала: </span>
                                    <span>{{task.startDate | moment('D.MM.YY')}}</span>
                                </p>
                                <p class="text-right">
                                    <span style="font-size: .7em; opacity: .7">Дедлайн: </span>
                                    <span>{{task.deadline | moment('D.MM.YY')}}</span>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div class="task-footer px-4 d-flex justify-content-between">
                        <div class="cost vertical-align-top">
                            <span style="opacity:.5; font-size: .7em">Оплата</span><br/><span style="font-size: 1.2em;">{{task.cost}}</span>₸
                        </div>
                        <router-link :to="'ongoing-tasks/'+task.id">
                            <el-button size="mini" plain>
                                Открыть
                            </el-button>
                        </router-link>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import {GET_ONGOING_TASKS, TASKS} from "../../store/mutation-types";
    export default {
        name: 'company-ongoing-tasks',
        computed: {
            tasks() {
                return this.$store.getters[TASKS + GET_ONGOING_TASKS]
            }
        },
        methods: {
            getPercentage(start, end) {
                const startDate = new Date(start).getTime()
                const endDate = new Date(end).getTime()
                const today = (new Date()).getTime()
                return parseInt((today - startDate)/(endDate - startDate) * 100)
            }
        },
        mounted() {
            this.$store.dispatch(TASKS + GET_ONGOING_TASKS)
        }
    }
</script>