<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" :rules="rules" status-icon>
            <el-form-item class="a-ui" prop="vacancyField" label="Сфера деятельности">
                <el-input placeholder="Мобильное приложение, фронтенд..." v-model="form.vacancyField"></el-input>
            </el-form-item>
            <el-form-item class="a-ui" prop="vacancyName" label="Название задачи">
                <el-input placeholder="Верстка сайта, разработка..." v-model="form.vacancyName"></el-input>
            </el-form-item>
            <el-form-item class="a-ui" prop="description" label="Описание">
                <wysiwyg class="mt-5" v-model="form.description" placeholder="Опишите вашу задачу"></wysiwyg>
            </el-form-item>
            <el-form-item class="a-ui" prop="demands" label="Требования">
                <el-select
                        v-model="form.demands"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        placeholder="Вводите требования"
                        class="w-100"
                >
                    <el-option
                        v-for="(item, i) in demandOptions"
                        :key="i"
                        :label="item.label"
                        :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <div class="row">
                <div class="col-md-6">
                    <el-form-item class="a-ui" prop="cost" label="Оплата">
                        <div style="width: 100%; overflow: auto">
                            <el-input-number :min="1000" :step="500" v-model="form.cost"></el-input-number>
                        </div>
                    </el-form-item>
                </div>
                <div class="col-md-6">
                    <el-form-item class="a-ui" prop="deadline" label="Срок сдачи">
                        <div style="width: 100%; overflow: auto">
                            <el-date-picker
                                    v-model="form.deadline"
                                    type="date"
                                    format="dd-MM-yyyy"
                                    placeholder="Выберите день"
                                    :picker-options="{
                                        firstDayOfWeek: 1,
                                        disabledDate: checkDate
                                    }"
                            >
                            </el-date-picker>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="text-right">
                <el-button @click="clearForm('form')">Отмена</el-button>
                <el-button type="primary" @click="submitForm('form')">Создать</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    import { CREATE_VACANCY } from '../../store/mutation-types';
    import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item";
    import ElInputNumber from "../../../node_modules/element-ui/packages/input-number/src/input-number";
    import {VACANCIES} from "../../store/types/vacancies";
    import {demandOptions} from "../../store/constants/index";

    export default {
        components: {
            ElInputNumber,
            ElFormItem},
        name: 'new-vacancy',
        data() {
            return {
                loading: false,
                types: [
                    {label: 'Полная занятость', value: 'Полная занятость'},
                    {label: 'Частичная занятость', value: 'Частичная занятость'},
                    {label: 'Проектная работа', value: 'Проектная работа'},
                    {label: 'Стажировка', value: 'Стажировка'},
                    {label: 'Практика', value: 'Практика'}
                ],
                demandOptions,
                form: {
                    vacancyField: '',
                    vacancyName: '',
                    description: '',
                    demands: [],
                    cost: 100000,
                    deadline: new Date(new Date().getTime() + (1000 * 60 * 60 * 24 * 3))
                },
                rules: {
                    vacancyField: [
                        { required: true, message: 'Заполните это поле', trigger: 'blur' },
                    ],
                    vacancyName: [
                        { required: true, message: 'Заполните это поле', trigger: 'blur' },
                    ],
                    cost: [
                        { required: true, message: 'Это объязательное поле', trigger: 'change'},
                    ],
                    deadline: [
                        { required: true, message: 'Это объязательное поле', trigger: 'change'},
                    ]
                }
            }
        },
        computed: {
        },
        methods: {
            checkDate(d) {
                return d.getTime() < (new Date()).getTime() + (1000 * 60 * 60)
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true
                        if (this.form.description === '') {
                            delete this.form.description
                        }
                        this.$store.dispatch(VACANCIES + CREATE_VACANCY , this.form).then(() => {
                            const h = this.$createElement;
                            this.$message({
                                message: h('p', null, [
                                    h('span', null, 'Задача '),
                                    h('i', { style: 'color: teal' }, `"${this.form.vacancyName}"`),
                                    h('span', null, ' успешно создана'),
                                ]),
                                type: 'success'
                            });
                            this.loading = false
                            setTimeout(() => {
                                this.$emit('success', true);
                            }, 1000)

                        })
                    } else {
                        this.$alert('Проверьте правильность всех полей', {
                            confirmButtonText: 'OK',
                        })
                            .then()
                        return false
                    }
                })
            },
            clearForm(formName) {
                this.$emit('cancel', true);
                this.$refs[formName].resetFields()
            }
        }
    }
</script>
