<template>
    <div class="profile">
        <div class="container">
            <el-container>
                <nav-panel class="d-none d-md-block"></nav-panel>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </div>
    </div>
</template>

<script>
    import NavPanel from '../components/NavPanel.vue'
    import {
        ADD_TO_CHAT,
        ADD_TO_CONVERSATION, CHAT,
        COMPANY,
        CONNECT_SOCKET,
        CONVERSATIONS,
        GET_SOCKET,
        MESSAGES,
        USER
    } from "../store/mutation-types"
    import {USER_PROFILE, GET_PROFILE} from "../store/types/userProfile"
    import {COMPANY_PROFILE} from "../store/types/companyProfile"
    import {baseURL} from "../store/api/main"
    import {AUTH, GET_TOKEN, GET_TYPE} from "../store/types/auth"
    import messageIn from "../assets/audio/in.mp3"

    export default {
        name: 'dashboard',
        components: {
            NavPanel
        },
        data() {
            return {
                USER: USER,
                COMPANY: COMPANY,
                socketAuthenticated: false,
            }
        },
        computed: {
            socket() {
                return this.$store.getters[MESSAGES + GET_SOCKET]
            },
            isLogged() {
                return this.$store.getters['auth/isLogged'] // Проверка на логин через store
            },
            userRole() { return this.$store.getters[AUTH + GET_TYPE] },
            profile() {
                return this.profileStore ? this.$store.getters[this.profileStore + GET_PROFILE] : false
            },
            profileStore() {
                if (this.isLogged === COMPANY) return COMPANY_PROFILE
                if (this.isLogged === USER) return USER_PROFILE
                return false
            },
            isConfirmed() {
                return (this.profile && this.profile.confirmed)
            }
        },
        beforeMount() {
            // Временный обход security роутинга, для не зарегестрированных пользователей
            const isLogged = this.$store.getters['auth/isLogged']
            if (!isLogged) {
                this.$router.push('/')
            } else if (isLogged === USER) {
                this.$store.dispatch(USER_PROFILE + GET_PROFILE)
            } else if (isLogged === COMPANY) {
                this.$store.dispatch(COMPANY_PROFILE + GET_PROFILE)
            }
        },
        methods: {
            drawNotification(message) {
                const notify = this.$notify({
                    title: message.authorId,
                    message: message.text,
                    position: 'bottom-right',
                    iconClass: 'chat-image',
                    offset: 50,
                    onClick: () => {
                        this.$router.push('/profile/chat/' + message.conversationId)
                        notify.close()
                    }
                })
                const icon = notify.$el.getElementsByClassName('el-notification__icon')[0]
                icon.style.backgroundImage = 'url(' + message.image + ')';
                (new Audio(messageIn)).play()
            }
        },
        mounted() {
            this.$store.dispatch(MESSAGES + CONNECT_SOCKET, {
                token: this.$store.getters[AUTH + GET_TOKEN],
                userType: this.userRole,
            });
            this.socket.on('chat_message', (data) => {
                data.image = `${baseURL}/${data.authorType}/image-avatar/${data.authorId}.png`
                if (this.$route.fullPath !== '/profile/chat/' + data.conversationId) this.drawNotification(data)
                this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {chatId: data.conversationId, message: data.text})
                this.$store.commit(CHAT + ADD_TO_CHAT, {messages: [data], chatId: data.conversationId})
            });
        },
    }
</script>