<template>
    <div class="p-0">
        <user-ongoing-tasks v-if="isLoaded && isLogged === USER"></user-ongoing-tasks>
        <company-ongoing-tasks v-if="isLoaded && isLogged === COMPANY"></company-ongoing-tasks>
        <div class="ongoing-tasks-placeholder py-5" v-if="!isLoaded">
            <h2 class="mb-5 px-2">Задачи</h2>
            <ul class="ongoing-tasks row">
                <li class="col-md-6 col-lg-4 mb-5" v-for="i in [1,2,3,4]" :key="i">
                    <div class="ongoing-task">
                        <div class="d-flex task-header">
                            <div class="text-center" style="min-width: 80px">
                                <div class="round-image loading-placeholder"></div>
                            </div>
                            <div class="task-title mt-2 py-1 ">
                                <h4 class="mb-2 loading-placeholder" style="width: 150px; height: 20px;"></h4>
                                <p class="loading-placeholder" style="width: 120px; height: 12px;"></p>
                            </div>
                        </div>
                        <div class="task-content px-4 py-3">
                            <div class="d-flex align-items-center">
                                <div class="task-progress">
                                    <el-progress type="circle" :percentage="37" :show-text="false" color="#e3e5e7"></el-progress>
                                </div>
                                <div class="task-info pl-3">
                                    <p class="loading-placeholder" style="width: 70px; height: 12px;"></p>
                                    <p class="loading-placeholder" style="width: 60px; height: 12px;"></p>
                                </div>
                            </div>
                        </div>
                        <div class="task-footer px-4 d-flex justify-content-between align-items-center">
                            <p class="loading-placeholder" style="width: 60px; height: 12px"></p>
                            <div style="height: 25px; width: 70px; border-radius: 5px" class="loading-placeholder"></div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import {COMPANY, GET_STATUS, USER} from "../store/mutation-types";
    import {COMPANY_PROFILE} from "../store/types/companyProfile";
    import {USER_PROFILE} from "../store/types/userProfile";
    import ProfilePlaceholder from "../components/ProfilePlaceholder";
    import UserOngoingTasks from "../components/UserProfile/UserOngoingTasks";
    import CompanyOngoingTasks from "../components/CompanyProfile/CompanyOngoingTasks";

    export default {
        name: 'ongoing-tasks',
        components: {
            CompanyOngoingTasks,
            UserOngoingTasks,
            ProfilePlaceholder,
        },
        computed: {
            isLogged() {
                return this.$store.getters['auth/isLogged']
            },
            profileStore() {
                if (this.isLogged === COMPANY) return COMPANY_PROFILE
                if (this.isLogged === USER) return USER_PROFILE
                return false
            },
            isLoaded() {
                return this.profileStore ? this.$store.getters[this.profileStore + GET_STATUS] === 'success' : false
            }
        },
        data() {
            return {
                USER,
                COMPANY
            }
        },
    }
</script>