<template>
    <div class="chat-container" v-loading="requestStatus === 'loading'">
        <beautiful-chat
                v-if="chat"
                :participants="participants"
                :titleImageUrl="titleImageUrl"
                :onMessageWasSent="onMessageWasSent"
                :messageList="messageList"
                :newMessagesCount="newMessagesCount"
                :close="onClose"
                :open="onOpen"
                :isOpen="true"
                :showEmoji="true"
                :showFile="true"
                :showTypingIndicator="''"
                :colors="colors"
                :alwaysScrollToBottom="alwaysScrollToBottom">
        </beautiful-chat>
        <div class="chat-conversation-placeholder d-flex align-items-center justify-content-center" v-if="!chat">
            <div class="text-center">
                <i class="fas fa-comments"></i>
                <p>Выберите чат</p>
            </div>
        </div>
    </div>
</template>

<script>
    import {AUTH, GET_TYPE} from "../../store/types/auth";
    import {baseURL} from "../../store/api/main";
    import {
        ADD_TO_CHAT,
        ADD_TO_CONVERSATION,
        CHAT,
        CONVERSATIONS,
        GET_CHAT,
        GET_PROFILE_STORE, GET_SELECTED_CHAT, GET_SOCKET,
        GET_STATUS, MESSAGES, SET_SELECTED_CHAT
    } from "../../store/mutation-types";
    import {GET_PROFILE} from "../../store/types/companyProfile";
    import messageOut from '../../assets/audio/out.mp3';

    export default {
        name: 'chat-conversation',
        data() {
            return {
                newMessagesCount: 0,
                colors: {
                    header: {
                        bg: '#4e8cff',
                        text: '#ffffff'
                    },
                    launcher: {
                        bg: '#4e8cff'
                    },
                    messageList: {
                        bg: '#ffffff'
                    },
                    sentMessage: {
                        bg: '#4e8cff',
                        text: '#ffffff'
                    },
                    receivedMessage: {
                        bg: '#eaeaea',
                        text: '#222222'
                    },
                    userInput: {
                        bg: '#f4f7f9',
                        text: '#565867'
                    }
                },
                alwaysScrollToBottom: true, // when set to true always scrolls the chat to the bottom when new events are in (new message, user starts typing...)
                socketAuthenticated: false,
            }
        },
        props: {
            chat: {
                chatImage: String,
                chatId: String,
                chatMessage: String,
                chatName: String,
                chatReceiver: String,
                unread: Boolean,
                lastMessageId: String
            }
        },
        methods: {
            onMessageWasSent (message) {
                // called when the user sends a message
                const messageText = message.data.text
                const messageEnt = {
                    timeSent: new Date(),
                    authorId: this.userId,
                    messageType: 'text',
                    text: messageText,
                }
                this.socket.emit('chat_message', {
                         receiverId: this.chat.chatReceiver,
                         messageType: 'text',
                         text: messageText,
                         timeSent: new Date(),
                }, () => {
                    this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {message: messageText, chatId: this.chat.chatId})
                    this.$store.commit(CHAT + ADD_TO_CHAT, {messages: [messageEnt].slice(), chatId: this.chat.chatId})
                    const audio = new Audio(messageOut)
                    audio.play()
                })
            },
            onOpen() {},
            onClose() {},
            getImageUrl(role, id) {
                return `${baseURL}/${role}/image-avatar/${id}.png`
            },
            loadMessages() {
                this.$store.dispatch(CHAT + GET_CHAT, {
                    userType: this.userRole,
                    chatId: this.chat.chatId,
                    lastMessage: '_'
                })
            }
        },
        computed: {
            socket() {
                return this.$store.getters[MESSAGES + GET_SOCKET]
            },
            messageList() {
                const messages = this.$store.getters[CHAT + GET_SELECTED_CHAT]
                messages.slice().forEach(c => {
                    c.type = c.messageType;
                    c.author = this.userId === c.authorId ? 'me' : c.authorId;
                    c.data = {
                        text: c.text
                    }
                })
                return messages
            },
            profileStore() { return this.$store.getters[AUTH + GET_PROFILE_STORE]},
            userRole() { return this.$store.getters[AUTH + GET_TYPE] },
            userId() { return this.$store.getters[this.profileStore + GET_PROFILE].id },
            requestStatus() {return this.$store.getters[CHAT + GET_STATUS]},
            participants() {
                return [{
                    id: this.chat ? this.chat.chatReceiver : '',
                    name: this.chat ? this.chat.chatName : '',
                    imageUrl: this.chat ? this.chat.chatImage : '',
                }]
            },
            titleImageUrl() { return this.chat.chatImage},
        },
        mounted() {
            if (this.requestStatus === 'clear') {
                this.loadMessages()
            }
        },
        watch: {
            chat: function(to, from) {
                if (to && (!from || !from.chatId || to.chatId !== from.chatId)) {
                    this.loadMessages()
                    this.$store.commit(CHAT + SET_SELECTED_CHAT, to.chatId)
                }
            }
        }
    }
</script>

