import {
    GET_ONGOING_TASKS, GET_STUDENT_ONGOING_TASKS, STATUS_REQUEST, STATUS_CLEAN, STATUS_ERROR,
    STATUS_SUCCESS, SET_TASKS, GET_SELECTED_TASK, SET_SELECTED_TASK
} from '../mutation-types'
import api, {baseURL} from '../api/main'

const state = {
    list: [],
    selectedTask: '',
    status: 'clear',
}


const getters = {
    [GET_ONGOING_TASKS] (state) {
        return state.list.slice()
    },
    [GET_SELECTED_TASK] (state) {
        return state.list.slice().filter(t => t.id === state.selectedTask)[0]
    }
}

const mutations = {
    [STATUS_REQUEST] (state) {
        state.status = 'loading'
    },
    [STATUS_SUCCESS] (state) {
        state.status = 'success'
    },
    [STATUS_ERROR] (state) {
        state.status = 'error'
    },
    [STATUS_CLEAN] (state) {
        state.status = 'clean'
        state.all = {}
        state.list = []
        state.loadedList = []
    },
    [SET_TASKS] (state, payload) {
        payload.forEach(t => {
            t.id = t._id
            t.startDate = new Date(t.startDate)
            t.deadline = new Date(t.deadline)
            t.companyImage = baseURL + '/company/image-avatar/' + t.companyId + '.png';
            t.studentImage = baseURL + '/student/image-avatar/' + t.freelancerId + '.png';
            delete t._id
        })
        state.list = payload.slice()
    },
    [SET_SELECTED_TASK] (state, payload) {
        state.selectedTask = payload
    }
}
const actions = {
    [GET_ONGOING_TASKS]: ({commit}) => new Promise((resolve, reject) => {
        commit(STATUS_REQUEST)
        api.get('/company/vacancy/ongoing').then(res => {
            console.log(res)
            commit(SET_TASKS, res.data.tasks.slice())
            commit(STATUS_SUCCESS)
            resolve(res)
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
            reject(err)
        })
    }),
    [GET_STUDENT_ONGOING_TASKS]: ({commit}) => new Promise((resolve, reject) => {
        commit(STATUS_REQUEST)
        api.get('/student/vacancy/ongoing').then(res => {
            commit(SET_TASKS, res.data.tasks.slice())
            commit(STATUS_SUCCESS)
            resolve(res)
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
            reject(err)
        })
    })

}
export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}