<template>
    <div class="document d-flex justify-content-between align-items-center">
        <div class="document-info d-flex align-items-center">
            <div style="min-width: 70px" class="mr-1">
                <document-thumb class="mx-auto" :type="type" :link="document.fileInfo.link"></document-thumb>
            </div>
            <div>
                <h4>{{document.fileInfo.name}}</h4>
                <p style="opacity: 0.7" class="mb-0">{{typeLabels[type]}}</p>
            </div>
        </div>
        <div class="document-actions mx-3 text-right">
            <el-button-group>
                <el-button type="danger" icon="el-icon-delete" size="small" circle plain title="Удалить" @click="deleteDocument"></el-button>
                <el-button icon="far fa-eye" size="small" circle plain title="Посмотреть" v-if="type==='image' || type==='pdf'" @click="dialog=true"></el-button>
                <el-button icon="fas fa-download" size="small" round plain title="Скачать" @click="download()"></el-button>
            </el-button-group>
        </div>
        <el-dialog :visible.sync="dialog" v-if="type==='image'" style="margin-top: -10vh;margin-bottom:-10vh;">
            <img :src="document.fileInfo.link" alt="" width="100%">
        </el-dialog>
        <el-dialog :visible.sync="dialog" v-if="type==='pdf'" style="margin-top: -15vh;margin-bottom: -15vh;">
            <iframe :src="document.fileInfo.link" alt="" width="100%" style="height: 100vh"></iframe>
        </el-dialog>
    </div>
</template>

<script>
    import DocumentThumb from "./DocumentThumb";
    import {DELETE_DOCUMENT, DOCUMENTS} from "../../../store/mutation-types";
    export default {
        components: {DocumentThumb},
        name: 'document-item',
        data() {
            return {
                dialog: false,
                typeLabels: {
                    'pdf'   : 'Документ',
                    'word'  : 'Документ',
                    'image' : 'Изображение',
                    'none'  : 'Файл',
                }
            }
        },
        props: {
            document: {
                type: Object,
                required: true
            }
        },
        computed: {
            type() {
                if (this.document.fileInfo.mimeType === 'application/pdf')
                    return 'pdf';
                if (this.document.fileInfo.mimeType === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document')
                    return 'word';
                if (this.document.fileInfo.mimeType === 'application/msword')
                    return 'word';
                if (this.document.fileInfo.mimeType === 'image/png')
                    return 'image';
                if (this.document.fileInfo.mimeType === 'image/jpg')
                    return 'image';
                if (this.document.fileInfo.mimeType === 'image/jpeg')
                    return 'image';
                return 'none';
            }
        },
        methods: {
            download() {
                window.open(this.document.fileInfo.link,'_blank');
            },
            deleteDocument() {
                this.$confirm(`Удалить файл ${this.document.fileInfo.name}?`, 'Подтвердите действие', {
                    confirmButtonText: 'Да',
                    cancelButtonText: 'Отмена',
                    type: 'warning'
                }).then(() => {
                    this.$store.dispatch(DOCUMENTS + DELETE_DOCUMENT, {userType: 'student', documentId: this.document._id})
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .document {
        height: 70px;
        border: 1px solid #fafafa;
        border-radius: 4px;
        box-shadow: 0 3px 10px rgba(0,0,0,.08);
        overflow: hidden;
    }
    .document-actions {
        min-width: 110px;
        transform: translateX(120px);
        transition: .1s;
        .document:hover & {
            transform: translateX(0px);
        }
    }
    .el-dialog__wrapper /deep/ .el-dialog__body {
        padding: 0;
    }
    .el-dialog__wrapper /deep/ .el-dialog__header {
        padding: 0;
    }
</style>