import api from '../api/main'
import {
    ADD_DOCUMENTS, DELETE_DOCUMENT,
    GET_DOCUMENTS, SET_DOCUMENTS, STATUS_CLEAN, STATUS_ERROR, STATUS_REQUEST,
    STATUS_SUCCESS
} from "../mutation-types";
const state = {
    status: 'clean',
    list: []
}

const getters = {
    [GET_DOCUMENTS]: state => state.list.slice().reverse()
}

const actions = {
    [GET_DOCUMENTS]: ({commit}, userType) => {
        commit(STATUS_REQUEST)
        api.get(`${userType}/private/documents`).then(res => {
            commit(SET_DOCUMENTS, res.data.files)
            commit(STATUS_SUCCESS)
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
        })
    },
    [DELETE_DOCUMENT]: ({commit}, {userType, documentId}) => {
        commit(STATUS_REQUEST)
        api.delete(`${userType}/private/document/${documentId}`).then(() => {
            commit(DELETE_DOCUMENT, documentId)
            commit(STATUS_SUCCESS)
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
        })
    }
}

const mutations = {
    [STATUS_REQUEST]: state => {
        state.status = 'loading'
    },
    [STATUS_SUCCESS]: state => {
        state.status = 'success'
    },
    [STATUS_ERROR]: state => {
        state.status = 'error'
    },
    [STATUS_CLEAN]: state => {
        state.status = 'clean'
    },
    [SET_DOCUMENTS]: (state, files) => {
        state.list = files
    },
    [ADD_DOCUMENTS]: (state, files) => {
        state.list.push(...files)
    },
    [DELETE_DOCUMENT]: (state, documentId) => {
        const index = state.list.findIndex(d => d._id === documentId)
        if (index!==-1) state.list.splice(index, 1)
    }
}

export default  {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}