import api from '../api/main'
import {
    ADD_TO_CHAT,
    GET_CHAT, GET_REQUEST, GET_SAVED_CHAT, GET_SELECTED_CHAT, SET_CHAT, SET_SELECTED_CHAT,
    STATUS_CLEAN, STATUS_ERROR, STATUS_REQUEST, STATUS_SUCCESS
} from "../mutation-types";
const state = {
    status: 'clear',
    selectedChatId: null,
    list: [],
}

const getters = {
    [GET_CHAT]: (state) => state.list.slice(),
    [GET_REQUEST]: (state) => state.status,
    [GET_SAVED_CHAT]: (state) => (chatId) => {
        return state.all[chatId] ? state.all[chatId].slice().sort((a, b) => new Date(a.timeSent).getTime() - new Date(b.timeSent).getTime()) : []
    },
    [GET_SELECTED_CHAT]: (state) => {
        if (!state.selectedChatId) return []
        return state.list.slice().filter(m => m.chatId === state.selectedChatId).sort((a, b) => new Date(a.timeSent).getTime() - new Date(b.timeSent).getTime())
    }
}

const mutations = {
    [STATUS_REQUEST]: state => {
        state.status = 'loading'
    },
    [STATUS_SUCCESS]: state => {
        state.status = 'success'
    },
    [STATUS_ERROR]: state => {
        state.status = 'error'
    },
    [STATUS_CLEAN]: state => {
        state.status = 'clean'
    },
    [SET_CHAT]: (state, {chatId, messages}) => {
        messages.forEach(m => {
            m.timeSent = new Date(m.timeSent);
            m.chatId = chatId
            const i = state.list.findIndex(l => l._id === m._id);
            if (i!==-1) state.list[i] = m;
            else state.list.push(m);
        });
    },
    [ADD_TO_CHAT]: (state, {chatId, messages}) => {
        messages.forEach(m => {
            m.timeSent = new Date(m.timeSent);
            m.chatId = chatId
        });
        state.list.push(...messages);
    },
    [SET_SELECTED_CHAT]: (state, chatId) => {
        state.selectedChatId = chatId
    }
}

const actions = {
    [GET_CHAT]: ({commit}, {userType, chatId, lastMessage}) => {
        commit(STATUS_REQUEST)
        return api.get(`${userType}/chat/${chatId}/${lastMessage}/50`).then(res => {
            commit(SET_CHAT, {messages: res.data.messages, chatId})
            commit(STATUS_SUCCESS)
            return res.data.messages
        }).catch(err => {
            console.log(err)
            commit(STATUS_ERROR)
        })
    },
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}